.home-selection {
  padding-bottom: 80px;
  overflow: hidden;
  @include r(1024) {
    padding-bottom: 0; }
  .heading {
    @include r(1024) {
      margin-bottom: 40px; }
    @include r(767) {
      margin-bottom: 25px; } }
  &__block {
    margin-bottom: 50px;
    @include r(1024) {
      margin-bottom: 30px; }
    @include r(767) {
      margin-bottom: 50px; }
    img {
      margin-bottom: 10px;
      @include r(767) {
        margin-bottom: 0;
        height: 63px; } }
    p {
      font-size: 22px;
      line-height: 1.36;
      letter-spacing: 0.6px;
      text-align: center;
      color: $charcoal-grey-two;
      @include r(1024) {
        font-size: 20px; } } } }
