.catalog {
  padding-top: 50px;
  background-color: #fbfbfb;
  overflow: hidden;
  @include r(1024) {
    background: none;
    padding-top: 30px; }
  @include r(767) {
    padding-top: 0; }
  .home-hit__swiper {
    padding-bottom: 100px;
    background: $white;
    padding-top: 120px;
    @include r(1024) {
      padding-top: 30px;
      padding-bottom: 25px; }
    .swiper-button-next {
      @include r(767) {
        right: 0px; } }
    .swiper-button-prev {
      @include r(767) {
        left: 5px; } } }
  .heading {
    justify-content: flex-start;
    @include r(1024) {
      margin-bottom: 50px; }
    @include r(767) {
      margin-bottom: 30px; }
    h2, h1 {
      line-height: 1;
      text-align: left;
      padding-bottom: 10px;
      span {
        color: $lipstick-five; } } }
  a.btn {
    margin-top: 30px;
    @include center-relative-x;
    position: relative;
    @include r(767) {
      margin-top: 0px; } }
  .block__img {
    float: right;
    width: 345px;
    margin-left: 30px;
    margin-bottom: 90px;
    @include r(1024) {
      width: 315px;
      height: 315px; }
    @include r(767) {
      display: none; }
    &__content {
      img {
        width: calc(100% - 30px);
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); } } }
  &__filter {
    margin-bottom: 80px;
    @include r(1024) {
      margin-bottom: 40px; }
    .col-xs-6 {
      @include r(767) {
        padding-left: 0.7rem;
        padding-right: 0.7rem; } }
    &__block {
      height: 270px;
      border-radius: 25px;
      background-color: $white;
      box-shadow: 0px 2px 5px 0 rgba(0, 0, 0, 0.25);
      @include center-column;
      padding: 20px 10px;
      margin-bottom: 30px;
      position: relative;
      overflow: hidden;
      @include tr;
      @include r(1024) {
        min-height: 230px;
        height: auto; }
      @include r(767) {
        min-height: 130px;
        margin-bottom: 20px;
        padding: 15px 10px; }
      &:hover {
        transform: scale(1.07);
        box-shadow: 0px 2px 15px 0 rgba(0, 0, 0, 0.25); }
      img {
        height: 170px;
        @include r(1024) {
          height: 130px; }
        @include r(767) {
          height: 70px; } }
      p {
        font-size: 18px;
        font-weight: bold;
        line-height: 1.22;
        letter-spacing: 0.5px;
        text-align: center;
        color: $greyish-brown-three;
        padding-top: 15px;
        @include tr;
        &:hover {
          color: $rouge-two; }
        @include r(1024) {
          font-size: 15px; }
        @include r(767) {
          font-size: 8px;
          padding-top: 5px; } }
      span {
        font-size: 18px;
        font-weight: bold;
        line-height: 1.22;
        letter-spacing: 0.5px;
        text-align: center;
        color: $greyish-brown-three;
        padding-top: 5px;
        @include r(1024) {
          font-size: 15px; }
        @include r(767) {
          font-size: 8px;
          padding-top: 5px; }
        b {
          font-size: 21px;
          font-weight: bold;
          line-height: 1.5;
          letter-spacing: 0.6px;
          color: $rouge-two;
          display: inline-block;
          @include r(1024) {
            font-size: 16px; }
          @include r(767) {
            font-size: 9px; } } } } }
  &__info {
    position: relative;
    z-index: 1;
    border-bottom: 1px solid rgba($dark, 0.15);
    padding-bottom: 25px;
    padding-top: 70px;
    @include r(1024) {
      padding: 40px 15px 0px 0; }
    @include r(767) {
      padding-top: 30px;
      padding-bottom: 0;
      padding-right: 0;
      h2 {
        font-size: 20px !important; } }
    .heading {
      @include r(1024) {
        margin-bottom: 40px; } }
    &:last-child {
      border-bottom: none; }
    &-secondary {
      .block__img {
        float: left;
        margin-right: 30px;
        margin-top: 20px; } }
    p {
      font-size: 15px;
      line-height: 1.47;
      letter-spacing: 0.4px;
      color: $dark-three;
      padding-bottom: 25px;
      @include r(1024) {
        font-size: 14px; }
      span {
        font-weight: 500;
        color: $lipstick-five; } }
    ul {
      margin-bottom: 30px; }
    li {
      font-size: 14px;
      font-weight: 300;
      line-height: 1.57;
      letter-spacing: 0.4px;
      color: $dark-three;
      a {
        font-weight: 500;
        color: $lipstick-five; } }
    .equipment__phone {
      justify-content: flex-start;
      @include r(1024) {
        justify-content: center;
        a {
          font-size: 60px; } }
      @include r(767) {
        margin: 0 0 25px;
        a {
          font-size: 30px; } } } } }
